.header-principal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: $zindex-fixed;

    display: flex;
    justify-content: space-between;

    border-bottom: solid 0.1rem $border-color;
    background-color: $white;
}

.header-principal--left,
.header-principal--right {
    display: flex;
    align-items: center;
    height: 7rem;
}

.header-principal--right {
    padding-right: 2rem;
    justify-content: flex-end;
}

.header-principal__nav {
    order: -1;
}

.header-principal__logo {
    padding: 1rem 2rem;
}

.header-principal__logo-link {
    display: flex;
    align-items: center;

    img {
        max-width: 6rem;
        max-height: 4.6rem;
        width: auto;
    }
}

.header-principal__name {
    margin-left: 1rem;

    font-family: $font-FiraSans;
    font-weight: 700;
    line-height: 1;
    text-transform: uppercase;
    color: $primary;
}

@include media-breakpoint-down(md) {
    // the logo is centered
    .header-principal--left {
        @include aw-make-col-auto();
    }

    .header-principal__logo {
        margin-left: auto;
        margin-right: auto;
    }
}

@include media-breakpoint-up(lg) {
    .header-principal--left {
        border-right: solid 0.1rem $border-color;

        .layout-navLeft & {
            flex: 0 0 30rem;
            max-width: 30rem;
        }
    }

    .header-principal--right {
        width: 100%;
    }

    .header-principal__logo {
        padding-left: 3rem;
        padding-right: 3rem;

        .layout-navLeft & {
            padding-left: 2rem;
        }
    }
}