.o-accordion {
    border-top: solid 0.1rem $border-color;
}

.o-accordion + .o-accordion,
.paragraph + .paragraph .o-accordion // drupal adds div.paragraph around .o-accordion
{
    border-top: 0;
}

.o-accordion-global-title {
    margin: 0;
    padding: 2rem;
    font-size: 2rem;
    background-color: $primary-light;
}

.o-accordion__title {
    margin: 0;
    font-size: inherit;
}

.o-accordion__header {
    position: relative;
    display: block;
    width: 100%;
    text-align: left;

    padding: 2rem 4rem 2rem 2rem;
    font-size: 1.8rem;
    font-weight: 500;
    color: $gray-600;
    border-bottom: solid 0.1rem $border-color;

    transition: color 250ms ease, background-color 250ms ease;

    @include icon(after, icon-plus) {
        position: absolute;
        top: 50%;
        right: 1rem;
        transform: translate(0, -50%);
        font-size: 1.5rem;
        color: $gray-600;
    }

    &[aria-expanded="true"] {
        font-weight: 800;
        color: $body-color;

        @include icon(after, icon-minus, false);
    }

    [class*="icon"] {
        margin-right: 1rem;
        font-size: 80%;
    }

    @include hover {
        background-color: $gray-100;
    }

    .is-highlighted & {
        margin-top: -0.1rem;
        font-size: 2rem;
        font-weight: 500;
        color: $primary;
        border: solid 0.1rem $primary;

        &::after {
            color: inherit;
        }
    }
}

.o-accordion__panel {
    padding: 1.5rem;
    border-right: solid 0.1rem $border-color;
    border-bottom: solid 0.1rem $border-color;
    border-left: solid 0.5rem $primary;

    &[aria-hidden="true"] {
        display: none;
    }

    .wysiwyg {
        h2, h3 {
            margin-top: 0;
        }
    }

    .m-encart-address__iframe {
        margin: 0;
    }
}

@include media-breakpoint-up(lg) {
    .o-accordion-global-title {
        padding: 3.5rem 3rem;
        font-size: 2.5rem;
    }

    .o-accordion__header {
        padding: 3.5rem 5.5rem 3.5rem 3rem;
        font-size: 2rem;

        .is-highlighted & {
            font-size: 2.5rem;

            [class*="icon"] {
                margin-right: 2rem;
            }
        }
    }

    .o-accordion__header[aria-expanded]:after {
        right: 2rem;
    }

    .o-accordion__panel {
        padding: 2.5rem;
    }
}