.m-download-files {
    margin: 3rem 0;

    font-family: $font-FiraSans;
    font-weight: 500;
    line-height: 2;
}

.m-download-file__item {
    margin-bottom: 2rem;
    padding-bottom: 2.5rem; // space for .m-download-file__info
}

.m-download-file__link {
    position: relative;
    display: block;
    padding-left: 2.6rem;

    @include link-underline($underline: true, $element: ".m-download-file__text");

    @include icon(before, icon-download) {
        position: absolute;
        top: 0.5rem;
        left: 0;
        font-size: 1.6rem;
    }

    &:visited {
        color: $gray-600;

        &:hover,
        &:active {
            color: $link-hover-color;
        }
    }

    .icon-external-link {
        margin-left: 0.2rem;
    }
}

.m-download-file__info {
    position: absolute;
    top: 100%;
    left: 2.6rem;

    font-size: 1.3rem;
    font-weight: 300;
    color: $body-color;
}

// removing space top
.paragraph--type--image-texte + .paragraph--type--documents  {
    .m-download-files:nth-of-type(1) {
        margin-top: 0;
    }
}