.subfooter {
    padding: 1rem 0 2rem 0;
}

.subfooter__title {
    margin-top: 3rem;
    font-size: 2rem;
}

.subfooter__desc {
    font-style: italic;
    text-align: left;
}


.subfooter-ENSG {
    .subfooter-ENSG__logos {
        img {
            display: block;
            margin-bottom: 3rem;

            &:last-child{
                margin-bottom: 0;
            }
        }
    }
}
@include media-breakpoint-up(lg) {
    .subfooter {
        padding: 3rem 0;
    }

    .subfooter__title {
        font-size: 2.5rem;
        }

    // ENSG subfooter NL
    .subfooter-ENSG {
        .subfooter-ENSG__logos {
            img {
                display: inline-block;
                margin-right: 7rem;
                margin-bottom: 0;

                &:last-child{
                    margin-right: 0;
                }
            }
        }
    }
}