.navbar {
    .navbar-toggler {
        width: 7rem;
        height: 7rem;

        background-color: $primary;
        color: $white;
        outline-color: $body-color;

        border: 0;
    }

    .navbar-toggler--icon {
        font-size: 2rem;
    }
}

.navbar-toggler--text {
    display: block;

    font-family: $font-FiraSans;
    font-weight: 600;
    text-transform: uppercase;
}

.navbar-collapse {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: $zindex-fixed;
    overflow-y: auto;
    background-color: $white;

    visibility: hidden;
    transform: translateX(-100%);

    // remove the nav from the tab order when the nav is closed
    .nav-is-closed & .os-content {
        visibility: hidden;
    }

    .nav-is-open & {
        visibility: visible;
        transform: translateX(0);
    }

    .nav-item {
        border-bottom: solid 0.1rem $border-color;
    }

    .nav-link,
    .btn-icon--mobile {
        padding: 2.5rem 5rem 2.5rem 3rem;
        border-right: solid 0.5rem transparent;
    }

    .btn-icon--mobile {
        display: flex;
        align-items: center;

        [class*="icon-"] {
            margin-right: 1rem;
            font-size: 2rem;
            line-height: 1;
        }
    }

    // Submenu Lvl2
    .nav-collapse {
        margin: 0;
        border: 0;
        color: $navbar-light-color;

        .nav-submenu-link {
            display: block;
            padding: 1rem 2rem 1rem 5rem;
            border-right: solid 0.5rem transparent;
            color: inherit;

            &:after {
                right: 0.5rem;
            }
        }
    }

    // Submenu Lvl3
    .nav-submenu .nav-submenu {
        .nav-submenu-link {
            padding-left: 7rem;
            font-size: 1.4rem;
            font-weight: 400;
        }
    }
}

// common for all navs (navLeft and navPortails, mobile and desktop)
.navbar-nav {
    font-family: $font-FiraSans;
    font-weight: 500;

    // Hover
    .nav-link,
    .nav-submenu-link,
    .btn-icon--mobile {
        width: 100%;
        font-weight: 500;
        text-align: left;
        color: $navbar-light-color;

        transition: color 250ms ease, background-color 250ms ease, border-color 250ms ease;

        @include hover {
            color: $navbar-light-hover-color;
            background-color: $gray-100;
            border-color: $gray-400;
        }
    }

    [aria-expanded] {
        position: relative;
        width: 100%;

        @include icon(after, icon-angle-right) {
            position: absolute;
            top: 50%;
            right: 1.5rem;
            transform: translate(0, -50%);

            color: $gray-400;

            transition: transform 250ms ease;
        }
    }

    [aria-expanded="true"] {
        &::after {
            transform: translate(0, -50%) rotate(90deg);
        }
    }

    // Active
    .is-active {
        &,
        &.show {
            > .nav-link,
            > .nav-submenu-link {
                font-weight: 800;
                color: $primary;
                background-color: $primary-light;
                border-color: $primary;

                @include hover {
                    color: $primary;
                }
            }
        }
    }
}

.navbar--desktop {
    display: none;
}

@include media-breakpoint-down(md) {
    .navbar-nav + .navbar-nav {
        border-top: solid 1rem $border-color;
    }
}

@include media-breakpoint-up(lg) {
    .navbar--mobile {
        display: none;
    }

    .navbar--desktop {
        display: block;
        margin-right: auto;

        &:not(.header-principal__lang) {
            @include aw-make-col-auto;
        }
    }

    .header-principal__lang {
        margin-right: 1rem;
    }

    .navbar-collapse {
        top: $header-height;
        width: $nav-left-width;
        border-right: solid 0.1rem $border-color;
    }

    .navbar-nav--portails,
    .navbar-nav--lang {
        flex-direction: row;

        .nav-link {
            display: flex;
            align-items: center;
            height: 7rem;
            padding: 0 2rem;

            border-bottom: solid 0.5rem transparent;
        }
    }

    .navbar-nav--lang {
        .nav-link {
            position: relative;
            padding: 0 1rem;

            &::after {
                content: "";
                position: absolute;
                top: 50%;
                right: 0;
                width: 0.1rem;
                height: 2rem;
                transform: translate(0, -50%);
                background-color: $border-color;
            }
        }
    }
}