.o-temoignage {
    .o-paragraph-temoignage {
        // Flèches
        .tns-controls {
            display: flex;
            flex-direction: row;
            justify-content: flex-end;
            position: absolute;
            right: 0;
            top: 5rem;
            z-index: 1029;
            button[data-controls] {
                position: relative;
                &[disabled] {
                    @include icon(before, icon-arrow-thin-right) {
                        color: rgba(#081624, .29);
                    }
                }
            }
            button[data-controls="prev"] {
                background-color: transparent;
                border: none;
                @include icon(before, icon-arrow-thin-right) {
                    color: $gray-900;
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%) scaleX(-1);
                }
            }
            button[data-controls="next"] {
                background-color: transparent;
                border: none;
                @include icon(before, icon-arrow-thin-right) {
                    color:  $gray-900;
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                }
            }
        }

        // bloc content
        .o-paragraph-temoignage__item {
            .o-paragraph-temoignage__item-inner {
                // position: relative;

                .o-temoignage__content {
                    padding: 12rem 3rem 8.2rem;
                    margin-top: -5rem;
                    .o-temoignage__text {
                        font-size: 1.5rem;
                        color: $gray-800;
                        line-height: 3rem;
                    }

                }
                .o-temoignage__img {
                    width: 16.5rem;
                    position: relative;
                    top: 3rem;
	                @include icon(before, icon-quote) {
                        position: absolute;
                        top: 2rem;
                        right: -2.5rem;
                        width: 5rem;
                        height: 4rem;
                        color: $primary;
						font-size: 4rem;
					}
                    // &:before {
                    //     content: "";
                    //     position: absolute;
                    //     top: 2rem;
                    //     right: -2.5rem;
                    //     width: 5rem;
                    //     height: 4rem;
                    //     background-image: url("../img/quote.svg");
                    //     background-color: transparent;
                    //     background-repeat: no-repeat;
                    //     background-size: contain;
                    // }
                }
            }
        }
        // bloc content bottom
        .o-temoignage__bottom {
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
            flex-direction: column;
            .o-temoignage__infos {
                display: flex;
                flex-direction: column;
                margin-bottom: 2rem;

                p {
                    margin-bottom: 0;
                    &.o-temoignage__author {
                        font-weight: $fw-bold;
                    }
                }
            }
        }
    }
}

// RWD Desktop/tablet
 @include media-breakpoint-up(lg) {
    .o-temoignage {
        h2 {
            margin-bottom: 3rem;
        }
        &.mt-temoignage {
            margin-top: 13rem;
        }
        .o-paragraph-temoignage {
            // Flèches
            .tns-controls {
                top: -7.5rem;


                button[data-controls="prev"] {
                   margin-right: 1rem;
                }
            }
            .o-paragraph-temoignage__item {
                .o-paragraph-temoignage__item-inner {
                    position: relative;
                    margin-left: auto;
                    width: calc(100% - 16.3rem);
                    .o-temoignage__content {
                        display: flex;
                        flex-direction: column;
                        justify-content: center;
                        padding: 3rem 5rem 3rem 14.2rem;
                        min-height: 45.8rem;
                        margin-top: 0;
                    }
                    .o-temoignage__img {
                        position: absolute;
                        left: -16.3rem;
                        top: 50%;
                        transform: translateY(-50%);
                        width: 24.5rem;
                    }
                }
            }

            // bloc content bottom
            .o-temoignage__bottom {
                flex-direction: row;
                align-items: flex-end;
            }
        }
    }
 }