.m-social__list {
    @include aw-make-row($grid-gutter-width);
}

.m-social__item {
    @include aw-make-col-ready($grid-gutter-width);
    @include aw-make-col-value(50%);
}

.m-social__link {
    display: block;
    text-align: center;
    color: inherit;

    @include hover-focus {
        color: inherit;
    }

    .icon-external-link {
        display: none;
    }
}

.m-social__icon {
    font-size: 3rem;
}

.m-social__text {
    margin-bottom: 1rem;
    line-height: 1;
}

.m-social__nb {
    display: block;
    font-size: 2rem;
    font-family: $font-FiraSans;
    font-weight: 800;
    line-height: 1;
}

@include media-breakpoint-up(sm) {
    .m-social__item {
        @include aw-make-col-auto;
    }
}