.m-media {
    margin: $paragraph-margin-bottom 0;

    &:not(.m-media-float) {
        img {
            @include img-full;
        }
    }

    a {
        display: block;
    }

    .icon-external-link {
        display: none;
    }
}

.m-media--full {
    margin: 3rem (-$grid-gutter-width);

    @include media-breakpoint-up(xl) {
        margin: 3rem 0 5rem 0;
    }
}

.figure-caption {
    padding: 0.5rem 0;
    font-style: italic;
}

.m-media-float {
    margin-top: 0;

    // width of <figcaption> match the width of <img>
    // https://stackoverflow.com/a/11652170
    figure {
        display: table;

        figcaption {
            display: table-caption;
            caption-side: bottom;
        }

        // fix bug IE: img overflow container inside table
        img {
            width: 100%;
        }
    }

    @include media-breakpoint-down(xs) {
        float: none !important;
    }
}

@include media-breakpoint-up(sm) {
    .m-media-float {
        max-width: 50%;

        &.float-left {
            padding-right: 3rem;
        }

        &.float-right {
            padding-left: 3rem;
        }
    }
}