.m-share__list {
    margin-bottom: 0;
    display: inline-block;
}

.m-share__item {
    &:not(:last-child) {
        margin-bottom: 2rem;
    }
}

.m-share__link {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 3rem;
    height: 3rem;

    color: $gray-800;

    @include hover-focus {
        color: $primary;
    }

    .icon-external-link {
        display: none;
    }
}

.m-share__icon {
    font-size: 2rem;
    line-height: 1;
}

@include media-breakpoint-down(lg) {
    .m-share {
        display: none;
    }
}