.m-teaser-site {
    margin-bottom: 2rem;
    padding-top: 2rem;
    padding-bottom: 2rem;

    // bg color variantes
    &[class*="bg-"] {
        padding-left: 2rem;
        padding-right: 2rem;
    }

    &[class*="bg-univers"] {
        *:not(.btn) {
            color: $white;
        }

        .m-teaser-site__title a {
            &:hover,
            &:focus,
            &:active {
                .m-teaser-site__title-link {
                    border-bottom-color: $white;
                }
            }
        }
    }

    // the link is on the entire block
    &.link-is-global {
        position: relative;

        .m-teaser-site__btn a {
            transform: none;

            &::before {
                content: "";
                position: absolute;
                top: 0;
                right: 0;
                bottom: 0;
                left: 0;
            }
        }
    }
}

// color variantes
.univers--institut {
    @include teaser-site--univers(univers-institut-primary);
}
.univers--particulier {
    @include teaser-site--univers(univers-particulier-primary);
}
.univers--professionnel {
    @include teaser-site--univers(univers-professionnel-primary);
}
.univers--carto {
    @include teaser-site--univers(univers-carto-primary);
}
.univers--accent {
    @include teaser-site--univers(univers-accent-primary);
}

.m-teaser-site__title {
    margin-bottom: 1rem;
    font-family: $font-FiraSans;
    font-weight: 800;
    font-size: 2rem;
    line-height: normal;

    a {
        display: inline-block;
        color: inherit;

        @include link-underline($element: ".m-teaser-site__title-link", $color: $primary);
    }

    .hasIcon & {
        position: relative;
        margin-top: 1rem;
        padding-left: 5.5rem;
    }
}

.m-teaser-site__title-icon {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate(0, -50%);

    font-size: 4.4rem;
    line-height: 1;
}

.m-teaser-site__title-text {
    display: block;
}

.m-teaser-site__title-link {
    font-size: 1.5rem;
    font-weight: 500;
    color: $gray-600;

    @include icon(after, icon-arrow-right) {
        margin-left: 0.5rem;
        font-size: 1.2rem;
    }
}

.m-teaser-site__lead {
    margin-bottom: 1rem;
    font-size: 1.3rem;
    line-height: 2;
    color: $gray-800;
}

.m-teaser-site__btn {
    margin-top: 1rem;

    .btn {
        margin-top: 1rem;
        margin-right: 1rem;
    }
}

@include media-breakpoint-up(md) {
    .m-teaser-site {
        padding-top: 3rem;
        padding-bottom: 3rem;

        // bg color variantes
        &[class*="bg-"] {
            padding-left: 3rem;
            padding-right: 3rem;
        }
    }
}

@include media-breakpoint-up(lg) {
    .m-teaser-site__lead {
        font-size: $font-size-base;
    }
}