.modal--full {
    .modal-dialog {
        max-width: none;
    }

    .modal-content {
        width: 100vw;
        height: 100vh;
        overflow: auto;
        background: $white;
    }

    @include media-breakpoint-up(lg) {
        .btn-close {
            position: absolute;
            top: 2rem;
            right: 2rem;
            z-index: 2;
        }

        .modal-title {
            padding-top: 10rem;
            padding-bottom: 4rem;
        }
    }
}

.modal--hasPadding {
    .modal-body {
        padding: 0 2rem 2rem 2rem;
    }
}

.modal-header {
    flex-direction: column-reverse;
}

.modal-title {
    padding: 2rem;
    width: 100%;
    text-align: center;
}