.o-teaser-job {
    margin-bottom: 2rem;
    padding: 2rem;
    background-color: $body-bg;
    border-radius: 0.8rem;
    box-shadow: -0.1rem 0.6rem 1.8rem 0 rgba(66,78,95,0.1);

    .m-download-files {
        margin: 1rem 0 0 0;
    }

    .m-download-file__item {
        margin-bottom: 0;
    }
}

.o-teaser-job__title {
    margin-bottom: 0.2rem;
    font-size: 1.8rem;
    line-height: 1.5
}

.o-teaser-job__info {
    margin-bottom: 0.5rem;

    &--domaine {
        font-size: 1.6rem;
        color: $primary;
    }

    &--location {
        font-family: $font-FiraSans;
        font-weight: 800;
    }

    &--date {
        font-size: 1.2rem;
        font-style: italic;
    }
}

@include media-breakpoint-up(md) {
    .o-teaser-job {
        position: relative;
        display: flex;
        flex-direction: column;

        .m-download-files {
            margin-top: 0;
            align-self: flex-end;
        }
    }

    .o-teaser-job__title {
        flex: 1 1 100%;
        min-height: 9rem;
        font-size: 2rem;
    }

    .o-teaser-job__info {
        &--domaine {
            font-size: 1.8rem;
        }

        &--date {
            position: absolute;
            bottom: 2rem;
            left: 2rem;
        }
    }
}