//  *
//  * @example
//  *  @include reset-list();
//  *  <ul class="reset-list"> ... </ul>
//  *
@mixin reset-list() {
    list-style: none;
    margin: 0;
    padding: 0;

    li,
    ul li {
        padding-left: 0;

        &:before {
            content: "";
            display: none;
            margin: 0;
        }
    }
}

//  * Reset list only Level 1
//  *
//  * @example
//  *  @include reset-list--l1();
//  *  <ul class="reset-list--l1"> ... </ul>
//  *
@mixin reset-list--l1() {
    list-style: none;
    margin: 0;
    padding: 0;

    > li {
        padding-left: 0;

        &:before {
            content: "";
            display: none;
            margin: 0;
        }
    }
}

//  *
//  * @example
//  *  @include list-ul(red);
//  *  @include list-ol(green);
//  *
@mixin list-ul($color: inherit, $margin: 1.5rem, $icon: false, $styles: false) {
    list-style: none; // level 1

    > li {
        position: relative;
        margin-bottom: $margin;
        padding-left: 2.5rem;

        &:before {
            content: "";
            position: absolute;
            top: 1.3rem;
            left: 0;
            width: 1rem;
            height: 0.2rem;
            background-color: $color;
            /* Screenreaders should ignore that kind of elements =========== */
            -webkit-alt: "";
            speak: none;
        }
        @if $styles {
            @include icon(before, $icon) {
                top: 1rem;
                font-size: 1rem;
                color: $color;
            }
        }


        &.list-has-check {
            padding-left: 3.5rem;
            &:before {
                content: "";
                position: absolute;
                top: .6rem!important;
                left: 0;
                width: 2.5rem;
                height: 2.5rem;
                background-image: url("../img/check.svg");
                background-color: transparent;
                background-repeat: no-repeat;
                background-size: contain;
            }
        }
    }
}

@mixin list-ol($color: inherit, $margin: 1.5rem) {
    list-style: none;
    counter-reset: list; // level 1

    > li {
        counter-increment: list;
        counter-reset: subsection;
        position: relative;
        margin-bottom: $margin;
        padding-left: 2.5rem;

        &:before {
            content: counter(list, decimal);
            position: absolute;
            top: 0;
            left: 0;
            font-weight: 700;
            color: $color;
        }

        // level 2
        ol {
            counter-reset: sublist;

            > li {
                counter-increment: sublist;
                padding-left: 3.5rem;

                &:before {
                    content: counter(list, decimal) "." counter(sublist, decimal);
                }
            }
        }

        // level 3
        ol ol {
            counter-reset: subsublist;

            > li {
                counter-increment: subsublist;
                padding-left: 5rem;

                &:before {
                    content: counter(list, decimal) "." counter(sublist, decimal) "." counter(subsublist, decimal);
                }
            }
        }
    }
}
