// Aspect Ratio
// See: https://css-tricks.com/snippets/sass/maintain-aspect-ratio-mixin/
@mixin aspect-ratio($width, $height) {
    position: relative;
    overflow: hidden;

    &:before {
        content: "";
        display: block;
        padding-top: ($height / $width) * 100%;
    }
}

@mixin change-aspect-ratio($width, $height) {
    &:before {
        padding-top: ($height / $width) * 100%;
    }
}

@mixin aspect-ratio--item {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}