.o-sitemap {
    .wysiwyg a:not(.btn) {
        @include link-underline--color-neutral();
    }
}

.o-sitemap__title {
    font-size: 2.1rem;

    .wysiwyg & a {
        font-weight: inherit;
    }
}

.o-sitemap__list {
    font-family: $font-FiraSans;
    font-weight: 500;
    color: $breadcrumb-active-color;
}

@include media-breakpoint-up(lg) {
    .o-sitemap__title {
        font-size: 2.5rem;
    }
}