@charset "UTF-8";

// layouts.scss -- Page layouts for project
//

*,
*::after,
*::before {
    box-sizing: border-box;
}

body {
    position: relative;
    overflow-x: hidden;

    &.overflow {
        overflow: hidden;
    }
}

.body-wrapper {
    margin-top: $header-height; // space for fixed header
}

a {
    transition: color 250ms ease;
}

figure,
.figure {
    display: block;
    margin: 0; // reset bootstrap
}

iframe {
    display: block;
    width: 100%;
}

main {
    margin-bottom: 3rem;

    @include media-breakpoint-up(lg) {
        margin-bottom: 6rem;
    }
}

.container-content {
    padding-left: $grid-gutter-width;
    padding-right: $grid-gutter-width;
    margin-left: auto;
    margin-right: auto;
    max-width: 124rem + $grid-gutter-width*2;
}

// Space between components
.block-space {
    margin-top: $block-space;
    margin-bottom: $block-space;
}
.small-space {
    margin-top: $small-space;
    margin-bottom: $small-space;
}

// RWD tablet/desktop
@include media-breakpoint-up(sm) {
    .block-space {
        margin-top: $block-space-desktop;
        margin-bottom: $block-space-desktop;
    }

    .small-space {
        margin-top: $small-space-desktop;
        margin-bottom: $small-space-desktop;
    }
}

.js-menu-transition {
    .navbar-collapse {
        transition: transform 250ms ease, visibility 250ms ease;
    }
    .layout-navLeft {
        transition: margin 250ms ease;
    }
}

.js-scrollspy-target {
    @include clearfix;
}

@include media-breakpoint-down(md) {
    .nav-is-open {
        overflow: hidden;
    }
}

@include media-breakpoint-up(lg) {
    .nav-is-open .layout-navLeft {
        margin-left: $nav-left-width;
    }
}

// switch from 'container-content' to 'container-content--smaller' when page has the class .node-type-page
.container-content--smaller, .node-type-page .container-content {
    @include container-content--smaller;
    &:not(.container-content--fake-col) {
        @include media-breakpoint-up(xl) {
            position: relative;
            z-index: 1; // usefull for sticky links in page article
        }
    }
}

// switch from 'container-content--smaller' to 'container-content' when page has the class .node-type-homepage
.node-type-homepage .container-content--smaller {
    // container-content css
    padding-left: $grid-gutter-width!important;
    padding-right: $grid-gutter-width!important;
    max-width: 124rem + $grid-gutter-width*2!important;
}