.o-youtube-player {
    position: relative;
    margin: 3rem 0;
    text-align: center;
    line-height: 2;

    iframe {
        z-index: 1;
    }
}

.o-youtube-player__content {
    position: relative;
}

.o-youtube-player iframe,
.o-youtube-player__img {
    @include aspect-ratio--item();
}

.o-youtube-player__img {
    z-index: -1;

    @include gradient-overlay;

    img {
        @include img-cover;
    }
}

.o-youtube-player__text {
    display: flex;
    align-items: flex-end;
    padding: 2rem;
    text-align: left;

    @include aspect-ratio(16, 9);

    // min-height of .o-youtube-player is 16/9 ratio but it can be longer
    // https://keithjgrant.com/posts/2017/03/aspect-ratios/
    &::before {
        float: left;
    }
    &::after {
        content: " ";
        display: table;
        clear: left;
    }
}

.o-youtube-player__btn {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    width: 5rem;
    height: 5rem;

    font-size: 4rem;
    color: currentColor;

    @include hover {
        color: $primary;
    }
}

.o-youtube-player__disclaimer {
    margin-bottom: 0;
    padding: 1rem 2rem;
    text-align: left;
    background-color: $body-color;
}

@include media-breakpoint-up(lg) {
    .o-youtube-player__text {
        padding: 3rem 4rem;
    }
}