button {
    @include reset-btn;
}

.btn--plain {
    &.btn--primary {
        @include aw-button-variant(
            $color: $white, $background: $primary, $border: $primary,
            $hover-color: $white, $hover-background: $primary-dark, $hover-border: $primary-dark,
            $active-color: $white, $active-background: $primary-secondary, $active-border: $primary-secondary,
            $outline-color: $primary
        );
    }

    &.btn--accent {
        @include aw-button-variant(
            $color: $white, $background: $univers-accent-primary, $border: $univers-accent-primary,
            $hover-color: $white, $hover-background: $univers-accent-dark, $hover-border: $univers-accent-dark,
            $active-color: $white, $active-background: $univers-accent-secondary, $active-border: $univers-accent-secondary,
            $outline-color: $univers-accent-primary
        );
    }

    &.btn--white {
        @include aw-button-variant(
            $color: $primary, $background: $white, $border: $white,
            $hover-color: $primary, $hover-background: $gray-200, $hover-border: $gray-200,
            $active-color: $primary, $active-background: $white, $active-border: $white,
            $outline-color: $white
        );
    }

    &.btn--univers-institut {
        @include aw-button-variant(
            $color: $white, $background: $univers-institut-primary, $border: $univers-institut-primary,
            $hover-color: $white, $hover-background: $univers-institut-dark, $hover-border: $univers-institut-dark,
