    &.btn--univers-particulier {
        @include aw-button-variant(
            $color: $white, $background: $univers-particulier-primary, $border: $univers-particulier-primary,
            $hover-color: $white, $hover-background: $univers-particulier-dark, $hover-border: $univers-particulier-dark,
