            $outline-color: $univers-particulier-primary
        );
    }

    &.btn--univers-professionnel {
        @include aw-button-variant(
            $color: $white, $background: $univers-professionnel-primary, $border: $univers-professionnel-primary,
            $hover-color: $white, $hover-background: $univers-professionnel-dark, $hover-border: $univers-professionnel-dark,
