}

// when :active, btn scales down
.btn-active-effect {
    @include aw-button-active;
}

// buttons width
.btn-width--md {
    min-width: 16rem;
}

.btn-width--lg {
    min-width: 22rem;
}

.btn-icon--header {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    width: 4.5rem;
    height: 4.5rem;
    color: $gray-800;
    border: 0;

    [class*="icon"] {
        font-size: 2.2rem;
        line-height: 1;
    }

    @include hover-focus {
        color: $primary;
    }
}

.btn-icon-only {
    &,
    [class*="icon"] {
        display: flex;
        align-items: center;
        justify-content: center;
    }

    padding: 0;
    width: 5rem;
    height: 5rem;
    line-height: 1;
    font-size: 2.6rem;
}

// close
.btn-close {
    display: block;
    margin-left: auto;
    width: 5rem;
    height: 5rem;
    color: $gray-800;

    [class*="icon"] {
        font-size: 1.7rem;
    }

    @include hover-focus {
        color: $primary;
    }
}