.o-teaser {
    position: relative;
    margin-bottom: 2rem;
    background-color: $gray-100;
}

.o-teaser--hasTag:not(.o-teaser--hasImg) {
    .o-teaser__content {
        padding-top: 5rem;
    }
}

a.o-teaser__inner {
    display: block;

    &,
    &:hover,
    &:focus,
    &:active {
        color: inherit;
    }

    &:hover {
        img {
            transform: scale(1.05);
        }
    }
}

.o-teaser__img {
    overflow: hidden;
    @include aspect-ratio(16, 9);

    img {
        @include aspect-ratio--item;
        @include img-cover;
        transform: scale(1.001);
        transition: transform 1s ease;
    }
}

.o-teaser__content {
    padding: 2rem;
    background-color: $gray-100;

    .a-info::before {
        color: $primary;
    }

    .a-info-date {
        font-family: $font-FiraSans;
    }

    .a-info-address {
        // font-style: italic;
    }

    .a-info-text {
        color: $gray-600;
        font-style: italic;
    }

    .a-info-type {
        min-height: 2.2rem;
    }
}

.o-teaser__title {
    margin-bottom: 1rem;
    font-size: 1.8rem;
    line-height: 1.5;
}

.o-teaser__tag {
    position: absolute;
    top: 0;
    left: 0;
}

.o-teaser__lead {
    margin-bottom: 1rem;
    font-size: 1.3rem;
    line-height: 2;
    color: $gray-800;
}

.o-teaser__link {
    text-align: right;
}

.o-teaser__breadcrumb {
    margin-bottom: 0.5rem;
    font-family: $font-FiraSans;
    font-weight: 800;
}

.o-teaser__breadcrumb-item:not(:last-child) {
    @include icon(after, icon-angle-right) {
        margin: 0 0.2rem 0 0.5rem;
        font-size: 1rem;
    }
}

.o-teaser--calendar {
    .o-teaser__title {
        margin-bottom: 0;
        color: $primary;

    }
    .a-date {
        padding-top: 2rem;
        padding-bottom: 2rem;
    }

    .o-teaser__content-bottom {
        display: flex;
        flex-wrap: wrap;
        margin-top: 1rem;

        > * {
            margin-right: 2rem;
        }
    }

    .btns {
        padding-bottom: 3rem;
        text-align: center;
    }
}

@include media-breakpoint-up(md) {
    .o-teaser--horizontal {
        .o-teaser__inner {
            display: flex;
            min-height: 20rem;
        }

        .o-teaser__img {
            @include change-aspect-ratio(4, 3);
            @include aw-make-col-value(32.8rem);
            height: auto;
        }

        .o-teaser__content {
            @include aw-make-col-auto;
        }
    }

    .o-teaser__content {
        padding: 3rem;
    }

    .o-teaser__title {
        font-size: 2rem;
    }

    .o-teaser--highlight {
        .o-teaser__img {
            @include change-aspect-ratio(980, 435);
            height: 43.5rem;
        }

        .o-teaser__content {
            position: absolute;
            left: 0;
            bottom: 0;
            width: calc(50% - 1rem);
        }
    }

    .o-teaser--calendar {
        .a-date {
            min-width: 20rem;
            // min-height: 20rem;
        }

        .btns {
            padding-bottom: 0;
            padding-right: 3rem;
            align-self: center;
        }
    }
}

@include media-breakpoint-up(lg) {
    .o-teaser__lead {
        font-size: $font-size-base;
    }
}