// Copy bootstrap mixins and removing unnecessary code

// Button variants
//
// Easily pump out default styles, as well as :hover, :focus, :active,
// and disabled options for all buttons

@mixin aw-button-variant(
    $color, $background, $border,
    $hover-color, $hover-background, $hover-border,
    $active-color, $active-background, $active-border,
    $outline-color
){
    color: $color;
    background-color: $background;
    border-color: $border;
    outline-color: $outline-color;

    @include hover-focus() {
        color: $hover-color;
        background-color: $hover-background;
        border-color: $hover-border;
        outline-color: $outline-color;

    }

    &:active {
        color: $active-color;
        background-color: $active-background;
        border-color: $active-border;
        outline-color: $outline-color;
    }

    .icon-external-link {
        color: currentColor !important;
    }
}


// Button sizes
@mixin button-size($padding-y, $padding-x, $font-size, $line-height, $border-radius) {
  padding: $padding-y $padding-x;
  @include font-size($font-size);
  line-height: $line-height;
  // Manually declare to provide an override to the browser default
  @include border-radius($border-radius, 0);
}


// when :active, btn scales down
@mixin aw-button-active {
    transform: scale(1);

    &:active {
        transform: scale(0.95);
    }
}