.nav-secondary-wrapper {
    position: relative;
    // padding-top: 1rem;
    max-width: 100%;
    overflow: hidden;
    background-color: $primary;
    margin-bottom: 5rem;
}

 .nav-secondary-container {
    [aria-hidden="true"].nav-submenu-content {
        display: none;
    }
    //// Not needed any more  overriding tabsa11y js librarie
    // &.tabs a {
    //    display: inline-block!important;
    //    padding: $btn-padding-y $btn-padding-x!important;
    //}
}

.nav-secondary {
	overflow-x: auto;
	overflow-y: hidden;
	-webkit-overflow-scrolling: touch;

	-ms-overflow-style: -ms-autohiding-scrollbar; // If JS present, let's hide the default scrollbar

    // To not have the arrows over text
    // width: calc(100% - 7.4rem);
    // margin: auto;

    &::-webkit-scrollbar {
        display: none; // Remove the default scrollbar for WebKit implementations
    }

    .nav-submenu {
        float: left; // Important for buttons left/right
        display: flex;
        justify-content: center;
        margin-bottom: 0;
        transition: transform .2s ease-in-out;
        padding: 2.5rem 3rem;
        &.no-transition {
            transition: none;
        }
    }

    .nav-submenu-item {
        display: flex;
        justify-content: center;
        align-items: center;
		text-align: center;
        &:hover {
            a {
                background-color: darken($primary, 5%);
                color: $white;
            }
        }
    }

    .nav-submenu-link {
        display: block;
        max-width: 15.4rem;
        width: 15.4rem;
        text-align: center;
        max-height: 5rem;
        padding: 1.4rem;

        font-size: 1.4rem;
        font-weight: $fw-bold;
        color: $white;
        text-decoration: none;
        cursor: pointer;
        &::after {
            content: "";
            display: block;
            margin-top: 0.8rem;
            width: 100%;
        }

        &:hover {
            color: $primary;
        }

        &[aria-selected="true"]{
            background-color: darken($primary, 10%);

            &:hover {
                background-color: darken($primary, 5%);
                a {
                    color: $white;
                }
            }
        }
    }

}


.btn.btn-nav-controls {
    position: absolute;
    top: 50%;
    bottom: 0;
    z-index: 1;
    transform: translateY(-50%);
    background-color: rgba($gray-800, 0.1);
    border-radius: 0;
    padding: 1rem;

    /* Set the buttons invisible by default */
    visibility: hidden;
    opacity: 0;
    transition: opacity 250ms;

    &.btn--prev, &.btn--next {
        span {
            @include icon(before) {
                color: $white;
            }
        }
    }

    &.btn--prev {
        left: 0;

        [data-overflowing="both"] ~ &,
        [data-overflowing="left"] ~ & {
            visibility: visible;
            opacity: 1;

            [data-contrast="high-c"] & {
                background-color: $white;
                border: solid 0.1rem;
            }
        }
    }

    &.btn--next {
        right: 0;

        [data-overflowing="both"]  ~ &,
        [data-overflowing="right"] ~ & {
            visibility: visible;
            opacity: 1;

            [data-contrast="high-c"] & {
                background-color: $white;
                border: solid 0.1rem;
            }
        }
    }
}