// var-custom.scss -- Variables for project

// Font
$fonticon_folder:   'icomoon';
$fonticon_token:    'clmdy0';
$fonticon-path:     '../font/#{$fonticon_folder}/#{$fonticon-name}';

$font-FiraSans:     "Fira Sans", "Open Sans", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif;

// Weight
$fw-regular : 400;
$fw-medium : 500;
$fw-semiBold : 600;
$fw-bold : bold;
$fw-extraBold : 800;
$fw-900 : 900;

// Weight map
$typo-familles-weight: (
    "regular": $fw-regular,
    "medium": $fw-medium,
    "semiBold": $fw-semiBold,
    "bold": $fw-bold,
    "extraBold": $fw-extraBold,
    "900": $fw-900,
);

