.o-search-form {
    [type="submit"] {
        min-width: 6.4rem;
        padding-top: 0;
        padding-bottom: 0;
        line-height: 1;

        [class*="icon"] {
            font-size: 1.8rem;
        }
    }

    .form-actions {
        margin-bottom: -0.3rem;
        @include drupal-submit-into-search;
    }
}

.o-search-form--page {
    // style drupal form
    .form--inline {
        display: flex;
        align-items: flex-end;
        flex-wrap: wrap;
    }

    .form-item-search-api-fulltext {
        @include aw-make-col-auto;
        margin-left: auto;
        margin-bottom: $form-group-margin-bottom;
    }

    .form-actions {
        margin-bottom: 3rem !important;
        margin-right: auto;
    }

    .form-item-sort-by {
        order: 1;
        flex: 0 0 100%;
    }
}

.o-search--modal {
    max-width: 85rem;
    margin: auto;
    padding-bottom: 5rem;
}

.o-search-form--modal {
    form {
        margin-top: 2rem;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .form-actions {
        margin-bottom: 3rem !important;
        margin-right: auto;
    }

    .form-group {
        @include aw-make-col-auto;
    }
}

@include media-breakpoint-up(sm) {
    .o-search-form--page {
        + .o-teasers {
            margin-top: 1rem;
        }

        .o-search-form__filters {
            .form-control {
                margin-left: 1rem;
            }
        }

        // style drupal form
        .form-item-sort-by {
            margin-left: auto;
            display: flex;
            align-items: center;
            justify-content: flex-end;

            label {
                margin: 0 1rem 0 0;
            }

            select {
                display: inline-block;
                width: auto;
                vertical-align: middle;
            }
        }
    }
}

@include media-breakpoint-up(lg) {
    .o-search-form--page {
        .o-search-form__input {
            max-width: 72.2rem;
            margin-left: auto;
            margin-right: auto;
        }

        // style drupal form
        .form-item-search-api-fulltext {
            @include aw-make-col-value(66rem);
        }
    }
}
