.m-encart {
    margin: $paragraph-margin-bottom 0;
    padding: 3rem;
}

.m-encart__desc {
    text-align: left;
    font-style: italic;
}

.m-encart-address__iframe {
    position: relative;
    margin-top: 2.5rem;
    margin-bottom: 5rem;
    height: 34rem;
    overflow: hidden;

    > * {
        @include aspect-ratio--item;
        margin: 0;
    }
}

.m-encart-infos {
    margin-bottom: $paragraph-margin-bottom;
    padding: 3rem 2rem;
    background-color: $primary-light;
    border-top: solid 0.5rem $primary;

    .a-info {
        font-weight: 600;
    }
}

.m-encart-infos__title {
    margin-bottom: 2.5rem;
    font-size: 2rem;
    text-align: center;
}

.m-encart-infos__content {
    margin-top: 2rem;

    p {
        margin-bottom: 0;
    }

    ul {
        margin-top: 0;
        margin-bottom: 1rem;
    }

    li {
        margin-bottom: 0 !important;
    }
}

@include media-breakpoint-up(lg) {
    .m-encart {
        margin: 5rem 0;
        padding: 5rem 10.7rem;
    }

    .m-encart-address__iframe {
        margin-bottom: 8rem;
    }
}