.layout-sidebarLeft {
    .m-share {
        text-align: right;
    }
}

@include media-breakpoint-down(lg) {
    .layout-sidebarRight {
        .js-scrollspy-target.container-content {
            .container-content {
                padding: 0;
            }
        }
    }
}

@include media-breakpoint-up(xl) {
    .layout-sidebarLeft {
        .container-content--fake-col {
            padding-left: $grid-gutter-width + $layout-navLeft-width + $layout-cols-padding;
            padding-right: $layout-cols-padding + $grid-gutter-width;
        }

        .container-content--sidebar {
            padding-left: $layout-cols-padding + $grid-gutter-width;
        }

        .container-content--sidebar-inner {
            position: absolute;
            width: $layout-navLeft-width - $layout-cols-padding;

            &.affix {
                position: fixed;
                top: $header-height + 2rem;
            }
        }
    }

    .layout-sidebarRight {
        .container-content--fake-col {
            padding-right: $grid-gutter-width + $layout-navLeft-width + $layout-cols-padding;
            padding-left: $layout-cols-padding + $grid-gutter-width;
        }

        .js-scrollspy-target {
            .col-content .container-content {
                padding-right: $grid-gutter-width !important;
                padding-left: $layout-cols-padding !important;
            }

            .container-content--sidebar {
                padding: 0;
            }

            .col-sidebar {
                @include aw-make-col-value($layout-navLeft-width + $grid-gutter-width);
            }

            .col-content {
                @include aw-make-col-auto;
            }

            .col-sidebar,
            .col-content > :first-child {
                margin-top: 5rem;
            }
        }

        .breadcrumb-wrapper {
            margin-bottom: 0;
        }
    }

    .layout-sidebarLeft,
    .layout-sidebarRight {
        .o-paragraph-diaporama .container-content {
            @include container-content--smaller;
        }
    }
}