.m-socialwall {
    margin-bottom: 2rem;
    background-color: $gray-100;
}

.m-socialwall__img {
    text-align: center;

    a {
        display: block;
    }

    .icon-external-link {
        display: none;
    }
}

.m-socialwall__content {
    padding: 2rem;
}

.m-socialwall__account {
    position: relative;
    display: flex;
    align-items: center;
    margin-bottom: 1rem;
    padding-right: 5rem; // space for icons
}

.m-socialwall__account-img {
    margin-right: 0.8rem;
    width: 3.9rem;
    height: 3.9rem;
    border-radius: 100%;
    overflow: hidden;

    @include aw-make-col-value(3.9rem);
}

.m-socialwall__name {
    margin-bottom: 0;
    font-family: $font-FiraSans;
    font-size: 1.6rem;
}

.m-socialwall__date {
    margin-bottom: 0;
    font-size: 1.3rem;
    color: $gray-600;
}

.m-socialwall__icon-wrapper {
    .icon-external-link {
        display: none;
    }
}

.m-socialwall__icon {
    position: absolute;
    top: 0;
    right: 0;
    font-size: 3.4rem;
    line-height: 1;

    &.icon-twitter {
        color: $brand-twitter;
    }
    &.icon-facebook {
        color: $brand-facebook;
    }
    &.icon-linkedin {
        color: $brand-linkedin;
    }
    &.icon-instagram {
        color: $brand-instagram;
    }
}

.m-socialwall__actions {
    display: flex;
    align-items: center;
    border-top: 0.1rem solid $gray-300;

    .icon-external-link {
        display: none;
    }
}

.m-socialwall__action {
    display: flex;
    align-items: center;
    padding: 2rem;

    font-size: 1.3rem;
    color: $gray-600;

    [class*="icon"] {
        line-height: 1;
        margin-right: 0.5rem;
    }

    &:hover {
        color: $primary;
    }
}

.m-socialwall__actions-share {
    margin-left: auto;
}