::-moz-selection {
    color: $white;
    background-color: $body-color;
}

::selection {
    color: $white;
    background-color: $body-color;
}

html {
    font-size: 62.5%;
}

em {
    font-style: italic;
}

h1, .h1 {
    color: $primary;

    @include media-breakpoint-up(lg) {
        font-size: 5rem;
    }
}

h2, .h2 {
    @include media-breakpoint-up(lg) {
        font-size: 3.5rem;
    }
}

h3, .h3 {
    @include media-breakpoint-up(lg) {
        font-size: 2.5rem;
    }
}

h4, .h4 {
    @include media-breakpoint-up(lg) {
        font-size: 2rem;
    }
}

h5, .h5 {
    @include media-breakpoint-up(lg) {
        font-size: 1.8rem;
    }
}

h6, .h6 {
    @include media-breakpoint-up(lg) {
        font-size: 1.6rem;
    }
}

.lead {
    margin-bottom: 2rem;
    line-height: 1.7;

    @include media-breakpoint-up(lg) {
        font-size: 1.8rem;
    }
}

h1, .h1,
.lead {
    text-align: center;
}

// p:last-child {
//     margin-bottom: 0;
// }

.external-link {
    .icon-external-link {
        display: inline-block;
        vertical-align: middle;
        margin-top: -0.4rem;
        margin-left: 0.6rem;
        font-size: 1rem;
        color: $primary;
    }
}

.mark, mark {
    color: $primary;
    font-weight: bolder;
}