.o-head-formation {
	&.block-space, &.small-space {
		margin-top: 0;
	}
}
.head-formation-item {
	display: flex;
	flex-direction: column;
	.head-formation__left {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: flex-end;
		padding: 8rem 3rem;
		min-height: 40rem;
		@include gradient-overlay;

		// background image
		.head-formation__img {
			height: 100%;
			width: 100%;
			object-fit: cover;
			position: absolute;
			transform: translate(-50%, -50%);
			left: 50%;
			top: 50%;
		}

		.head-formation__content {
			z-index: 2;

			.head-formation__subtitle {
				font-size: 1.8rem;
				font-weight: $fw-regular;
				text-transform: uppercase;
				margin-bottom: 0;
			}

			h1 {
				color: $white;
				margin-bottom: 3rem;
				text-align: left;
			}
			h3, p, a:not(.btn) {
				color: $white;
			}

			.head-formation__accroche {
				font-style: italic;
				font-size: 1.8rem;
				font-weight: $fw-semiBold;
				margin-bottom: 4rem;
			}

			.head-formation__actions {
				display: flex;
				justify-content: flex-start;
				flex-direction: column;
				text-align: center;

				.btns {
					a {
						margin: 1rem;
					}
				}
			}
		}
	}

	.head-formation__right {
		padding: 3rem;
		.head-formation__icons {
			.a-info {
				display: flex;
				flex-direction: column;
				margin-bottom: 2rem;
				position: relative;
				padding-left: 3rem;
				color: $gray-800;
            @include icon(before) {
					position: absolute;
    				left: 0;
					color: $primary;
					font-size: 2rem;
					vertical-align: bottom;
					margin-right: 1rem;
            };
				&:last-child {
					margin-bottom: 0;
				}
				.a-info__description {
					color: $gray-500;
					display: flex;
					font-size: 1.2rem;
				}
				&:not([class*='icon-']) {
					margin-bottom: .5rem;
				}
			}
		}
	}
}

// RWD Desktop/tablet
@include media-breakpoint-up(lg) {
	.head-formation-item {
		min-height: 40rem;
    	flex-direction: row;
		.head-formation__left {
			justify-content: flex-start;
			flex: 1 0 60%;
			padding: 8rem;

			.head-formation__content {
				text-align: left;
				margin-left: auto;
				width: 100%;
				max-width: 77rem;
				flex-direction: row;

				.head-formation__actions {
						flex-direction: row;
					}
				}
			}

		.head-formation__right {
			flex: 1 0 25%;
			padding: 0 0 0 3rem;
			// padding: 5rem 3rem;
			display: flex;
			align-items: center;
		}
	}
}


