// *
// * External link icon: position absolute to not have an underline
// *
@mixin external-link-icon-absolute() {
    margin-right: 2rem;

    .icon-external-link {
        position: absolute;
        margin-top: .4rem;
    }
}

// *
// * Style des liens avec couleur neutre (gris)
// *
@mixin link-underline--color-neutral() {
    @include link-underline($primary);

    &,
    &:hover,
    &:focus,
    &:active {
        color: inherit;
    }
}

// *
// * Gradient overlay
// *
@mixin gradient-overlay {
    &::after {
        content: "";
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background: linear-gradient(to top, rgba($black,0.5) 0%, rgba($black,0.2) 100%);
    }
}

// *
// * Megamenu color variantes
// *
@mixin megamenu-univers($univers-primary, $univers-primary-light) {
    > .megamenu__link {
        color: map-get($univers-colors, $univers-primary);

        &:hover,
        &[aria-expanded="true"] {
            background-color: map-get($univers-colors, $univers-primary-light);
            border-right-color: map-get($univers-colors, $univers-primary);
        }
    }
}

// *
// * .m-teaser-site color variantes
// *
@mixin teaser-site--univers($univers-primary) {
    .m-teaser-site__title a {
        @include link-underline($element: ".m-teaser-site__title-link", $color: map-get($univers-colors, $univers-primary));
    }

    .icon-external-link,
    .m-teaser-site__title-icon {
        color: map-get($univers-colors, $univers-primary);
    }
}

// *
// * Reduce .container-content width
// *
@mixin container-content--smaller {
    @include media-breakpoint-up(xl) {
        padding-left: $layout-cols-padding + $grid-gutter-width;
        padding-right: $layout-cols-padding + $grid-gutter-width;
        margin-left: auto;
        margin-right: auto;
    }
}

// *
// * Style drupal [type="submit"] like a button with icon-search
// *
@mixin drupal-submit-into-search {
    position: relative;

    @include icon(before, icon-search) {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: 1;
        font-size: 1.8rem;
        color: $white;
        pointer-events: none;
    }

    [type="submit"] {
        width: 6.4rem;
        height: $input-height;
        min-width: 0;
        text-indent: -9999em;
    }
}