.o-mosaique {
	h2 {
		text-align: left;
	}
	.lead {
		text-align: left;
		font-size: 1.5rem;
		font-weight: $fw-regular;
	}
	.mosaique-container {
		> * {
			display: flex!important;
			width: 100%;

			> * {
					width: 100%;
			}
		}

		.mosaique-item {
			position: relative;
			display: flex;
			flex-direction: column;
			justify-content: flex-end;
			// min-height: 33.6rem;
			// margin-bottom: 4rem;
			padding: 5rem 3rem 3rem;
			cursor: pointer;
			margin-bottom: 2rem;

			&.background-img {
				min-height: 32.6rem;

				.mosaique-content {
					h3, p, a:not(.btn) {
						color: $white;
					}
				}
				@include gradient-overlay;

				&:hover {
					&:after {
						background: rgba($primary, .74);
					}
				}
			}

			&[class*="bg-"] {
				padding: 6rem 3rem 4rem;
				min-height: 20rem;

				.mosaique-content {
					h3, p, a:not(.btn) {
						color: $black;
					}
					.mosaique-title {
						padding-right: 7rem;
					}
					[class*="icon-"] {
						color: $primary;
					}
				}
			}

			h3 {
				margin-bottom: 0;
			}
			// background image
			.mosaique-img {
				height: 100%;
				width: 100%;
				object-fit: cover;
				position: absolute;
				transform: translate(-50%, -50%);
				left: 50%;
				top: 50%;
			}

			// Floating content
			.mosaique-content {
				z-index: 2;
				position: relative;

				[class*="icon-"].a-info {
					margin-bottom: 1rem;
					@include icon(before) {
						font-size: 2.27rem;
					}
				}

				.mosaique-title {
					&+.mosaique-text {
						margin-top: .5rem;
					}
				}
				.mosaique-subtitle {
					font-size: 1.8rem;
					margin-bottom: 1rem;
					font-weight: $fw-semiBold;
				}

				.mosaique-text {
					margin-bottom: 2.3rem;
				}

				.mosaique-btn {
					margin-top: 2rem;
				}

				.mosaique-link {
					display: block;
					width: max-content;
					margin-top: 2rem;
					margin-left: auto;
				}
			}
		}
	}
}
// RWD Desktop/tablet
 @include media-breakpoint-up(md) {
	.o-mosaique {
		.mosaique-container {
			.mosaique-item {
				margin-bottom: 4rem;
			}
		}
	}
}