.o-teaser-site__list {
    padding-top: 2rem;

    &.o-teaser-site--overlap {
        margin-top: -5rem;
        padding-top: 0;
    }
}

@include media-breakpoint-up(md) {
    .o-teaser-site__list {
        @include aw-make-row(2rem);
    }

    .o-teaser-site__item {
        @include aw-make-col-ready(2rem);
        @include aw-make-col-value(50%);

        display: flex;

        > * {
            width: 100%;
        }
    }
}

@include media-breakpoint-up(lg) {
    .o-teaser-site__item {
        @include aw-make-col-value(33.333%);
    }
}
