.o-contacts {
	h2 {
		text-align: left;
		margin-bottom: 3rem;
	}
	.contacts-container {
		display: flex;
		flex-wrap: wrap;
		flex-direction: column;


		.contacts-item {
			position: relative;
			margin-bottom: 2rem;
			padding: 3rem 0;

			.contacts-content {
				.contacts-text {
					flex: 1;

					h4 {
						margin-bottom: 0;
					}
				}
			}
			&[class*="bg-"] {
				padding: 4rem 3rem 4rem;

				.contacts-content {
					.contacts-text {
						.contacts-text__bottom {
							padding-right: 0;
							> a.contacts-sendMail {
								text-decoration: underline;
							}
						}
					}
				}
			}
			// &:last-child {
			// 	margin-right: 0;
			// }

			.contacts-social {
				width: 2rem;
				max-width: 2rem;
				height: 2rem;

				.icon-external-link {
					display: none;
				}
				@include icon(before, icon-linkedin) {
					font-size: 2rem;
				}
			}
			.contacts-content {
				display: flex;
				flex-direction: column;

				img {
					max-width: 9rem;
					margin-right: 2.8rem;
					margin-bottom: 2rem;
					border-radius: 100%;
					@supports (object-fit: cover) {
						width: 9rem;
						height: 9rem;
						object-fit: cover;
					}
				}
				.contacts-text {
					p {
						line-height: 3rem;
						margin-bottom: 0;
					}

					.contacts-text__bottom {
						display: flex;
						justify-content: space-between;
						padding-right: 3rem;
					}
				}
			}
		}
	}
}

// RWD Desktop/tablet
 @include media-breakpoint-up(md) {
	.o-contacts {
		.contacts-container {
			flex-direction: row;
			.contacts-item {
				margin-bottom: 2rem;


				.contacts-social {
					position: absolute;
					right: 2rem;
				}
				&:nth-child(odd) {
					margin-right: 2rem;
				flex: 1 0 calc(50% - 2rem);
				width: calc(50% - 2rem);
				max-width: calc(50% - 2rem);
				}
				&:nth-child(even) {
					flex: 1 0 50%;
					width: 50%;
					max-width: 50%;
				}
				.contacts-content {
					flex-direction: row;
					img {
						margin-right: 2.8rem;
						margin-bottom: 0;
					}
				}
				// add a margin-top to all item after the 2nd
				// &:nth-child(n+3) {
				// 	margin-top: 2rem;
				// }
			}
		}
		&.contacts_fullCol {
			margin-bottom: 0;
			.contacts-container {
				.contacts-item {
					flex: 100%;
					width: 100%;
					max-width: 100%;
					margin-right: 0;
					&[class*="bg-"] {
						&:not(:last-child) {
							margin-bottom: 2rem;
						}
					}
				}
			}
		}
	}
}