.wysiwyg,
.cke_editable {
    p, ul, ol, dl {
        line-height: 2;
    }

    a:not(.btn) {
        @include hyphens;
        font-weight: 600;

        @include link-underline($underline: true);

        &:visited {
            color: $gray-600;

            &:hover,
            &:active {
                color: $link-hover-color;
            }
        }
    }

    .external-link:not(.btn) {
        @include external-link-icon-absolute;
    }

    h2, h3 {
        margin-top: $paragraph-margin-bottom;
    }

    ul, ol, dl {
        margin: $paragraph-margin-bottom 0 $paragraph-margin-bottom 2rem;
    }

    ul {
        @include list-ul($primary);

        ul {
            > li {
                padding-left: 2rem;

                &:before {
                    width: 0.5rem;
                }
            }
        }
    }

    ol {
        @include list-ol($primary);
    }

    dd {
        margin-bottom: 2rem;
        margin-left: 2rem;
    }

    //
    @include media-breakpoint-down(md) {
        font-size: 1.3rem;
    }

    @include media-breakpoint-up(xl) {
        h2, h3 {
            margin-top: 5rem;
        }

        ul:not([class])>li:before {
            top: 1.5rem;
        }
    }
}