.breadcrumb-wrapper {
    margin-bottom: $paragraph-margin-bottom;
}

.breadcrumb {
    align-items: center;

    font-family: $font-FiraSans;
    font-weight: 500;
    color: $breadcrumb-active-color;
}

.breadcrumb-item {
    & + .breadcrumb-item {
        @include icon(before, icon-angle-right) {
            margin-top: -0.2rem;
            vertical-align: middle;
            color: $breadcrumb-divider-color;
        }
    }

    a {
        @include link-underline--color-neutral();
    }
}


// + + + + + + + + + + + + + + + + + + + + + + + + + + + + + +
//
// MEDIA QUERIES
//
// + + + + + + + + + + + + + + + + + + + + + + + + + + + + + +
@include media-breakpoint-down(md) {
    .breadcrumb-wrapper {
        display: none;
    }
}

@include media-breakpoint-up(xl) {
    .breadcrumb-wrapper {
        margin-top: 4.6rem;
        margin-bottom: 4.2rem;
    }
}