.m-media-background {
    position: absolute !important;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
    overflow: hidden;

    @include gradient-overlay;

    video,
    img {
        position: absolute;
        top: 50%;
        left: 50%;
        right: auto;
        min-width: 100%;
        min-height: 100%;
        max-width: none;
        transform: translate3d(-50%, -50%, 0);
    }
}

.m-media-background__btn {
    position: absolute !important;
    bottom: 0;
    right: 0;
    font-size: inherit;
}