.sg-two-cols {
	.o-twoCols {
		margin: 4rem 0;
	}
}

.o-twoCols {
	h2 {
		margin-bottom: 3rem;
	}
	h3 {
		margin-bottom: 2rem;
	}

	.twoCols-block{
    	padding: 3rem 3rem 2rem;
		&:not([class*='bg-']) {
			padding: 0;
		}
	}
	.twoCols-container {
		display: flex;
		flex-wrap: wrap;
    	flex-direction: column;

		.twoCols-item {
			margin-right: 0;

			p {
				line-height: 3rem;
			}

			// a, p
			.wysiwyg {
				a, p {
					margin-bottom: 1.5rem;
				}

				p {
					line-height: 3rem;
				}

				ul {
					margin-top: 0;
					margin-bottom: 0;
				}
			}
		}
	}
}
// RWD Desktop/tablet
 @include media-breakpoint-up(md) {
	.o-twoCols {
		.twoCols-block{
 			padding: 5rem 4rem 3.5rem;
		}

	.twoCols-container {
    	flex-direction: row;
			.twoCols-item {
				flex: 1 0 calc(50% - 4rem);
				width: calc(50% - 4rem);
				max-width: calc(50% - 4rem);
				margin-right: 4rem;

				&:last-child {
					margin-right: 0;
				}
			}
		}
	}
}