// $diaporama-item-margin-bottom: 7rem;
$diaporama-item-margin-bottom: 3rem;

.o-paragraph-diaporama {
    position: relative;
    z-index: 1;
    // Replaced by class small-space from now on
    //margin-top: $paragraph-margin-bottom;
    //margin-bottom: $paragraph-margin-bottom;
    background-color: $body-bg;

    .tns-controls {
        display: inline-flex;
        position: absolute;
        bottom: $diaporama-item-margin-bottom;
        right: 0;
        width: 10rem;
        z-index: 1039;
    }

    .btn-controls {
        font-size: 1.6rem;

        &[disabled] {
            opacity: 0.4;
        }
    }
}

.o-paragraph-diaporama__item {
    position: relative;
    margin-bottom: $diaporama-item-margin-bottom; // space for <figcaption>
    height: 34.2rem;
}

.o-paragraph-diaporama__img {
    @include aspect-ratio--item();
    z-index: -1;

    img {
        @include img-cover;
    }

    figure {
        width: 100%;
        height: 100%;
    }

    figcaption {
        position: absolute;
        top: 100%;
        right: 0;
    }
}

.o-paragraph-diaporama__text {
    display: flex;
    align-items: flex-end;
    padding: 2rem 2rem 7rem 2rem;
    height: 100%;
    line-height: 2;

    @include gradient-overlay;
}

.o-paragraph-diaporama__title {
    margin-bottom: 0;
    line-height: 1.5;
}

.o-paragraph-diaporama__lead {
    margin-bottom: 1.5rem;
}

.o-paragraph-diaporama__text-inner {
    position: relative;
    z-index: 1;
}

.o-paragraph-diaporama__fullscreen {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
}

.o-paragraph-diaporama__item-wrapper:not(.tns-slide-active) {
    opacity: 0.7;

    .o-paragraph-diaporama__text,
    .o-paragraph-diaporama__fullscreen {
        visibility: hidden;
    }
}

@include media-breakpoint-up(lg) {
    .o-paragraph-diaporama__item {
        height: 41.3rem;
    }

    .o-paragraph-diaporama__text {
        padding: 3rem 4rem;
    }
}