@each $color, $value in $univers-colors {
    .text-#{$color} {
        color: $value;
    }

    .bg-#{$color} {
        background-color: $value;

        .btn--plain.btn--white {
            &,
            &:hover,
            &:focus,
            &:active {
                color: $value;
            }
        }
    }

}

@each $color, $value in $grays {
    .text-gray-#{$color} {
        color: $value;
    }

    .bg-gray-#{$color} {
        background-color: $value;
    }
}

.bg-gradient-primary {
    background: linear-gradient(to right, $primary 0%, $primary-secondary 100%);
}
.bg-primary-light {
    background-color: $primary-light;
}