.footer {
    padding-top: 4rem;

    color: $gray-600;
    background-color: $gray-900;

    a {
        color: inherit;

        @include link-underline();
    }
}

.footer-title {
    margin-bottom: 1rem;
    font-size: 2rem;
    color: $white;
}

.footer-address {
    margin-bottom: 1rem;
    line-height: 2;
    color: $white;
}

.footer-list {
    margin-bottom: 2rem;
    font-family: $font-FiraSans;
    font-weight: 500;

    .external-link {
        @include external-link-icon-absolute;
    }

    .icon-external-link {
        color: $white;
    }
}

.footer-list-item {
    padding: 1rem 0;
}

.footer-list-item--hasIcon {
    margin-left: 3rem;

    > a {
        position: relative;
    }
}

.footer-list-item__icon {
    position: absolute;
    top: 0;
    left: -3rem;

    font-size: 1.8rem;
    line-height: 1;
    color: $white;
}

.footer-list--inline {
    @include aw-make-row($grid-gutter-width);
    margin-bottom: 0;

    .footer-list-item {
        padding: 0 $grid-gutter-width*0.5;
    }
}

.footer-contentinfo {
    padding: 2.5rem 0;
    border-top: 0.1rem solid rgba($white, 0.1);

    .footer-list {
        margin-top: 0; // reset unnecessary margin in .footer-contentinfo
        justify-content: center;
    }
}

.footer-copyright {
    margin-bottom: 1rem;
    font-family: $font-FiraSans;
    font-weight: 500;
    text-align: center;
}

.footer-logo {
    padding: 4rem 0;
    border-top: solid 0.4rem $border-color;

    img {
        max-width: 14.5rem;
    }
}

@include media-breakpoint-up(md) {
    .footer-contentinfo {
        display: flex;
        align-items: center;
    }

    .footer-copyright {
        margin-bottom: 0;
        margin-right: $grid-gutter-width;
    }

    .footer-list {
        margin-top: 3.4rem; // align footer-list even if there is no title above
    }

    .footer-title + .footer-list,
    .footer-address + .footer-list {
        margin-top: 0;
    }
}

// Footer ENSG
.footer-logo-ENSG {
    .footer-logo-ENSG__container {
        display: flex;
        justify-content: center;
        flex-direction: column;
    }
    .logo-republique-ENSG {
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 5rem;
    }
    .footer-logo-ENSG__logos {
        display: flex;
        flex-direction: column;
        align-items: center;

        img  {
            margin-bottom: 5rem;

            &:last-child {
                margin-bottom: 0;
            }
        }
    }
}
@include media-breakpoint-up(lg) {
    .footer {
        padding-top: 6rem;
    }

    // Footer ENSG
    .footer-logo-ENSG {
        .footer-logo-ENSG__container {
            justify-content: flex-start;
            flex-direction: row;
        }
        .logo-republique-ENSG {
            margin: 0 9rem 0 0;
        }
        .footer-logo-ENSG__logos {
            flex-direction: row;
            // margin-left: 5rem;

            img {
                margin-bottom: 0;
                &:not(:last-child) {
                    margin-right: 6rem;
                }
            }

        }
    }
}


