.a-link--border-effect {
    font-weight: 600;

    @include link-underline-hover-effect();

    @include icon(after, icon-arrow-right) {
        font-size: 1.2rem;
        margin-left: 0.5rem;
    }

    &,
    &:hover,
    &:focus,
    &:active {
        color: $body-color;
    }
}

.a-link--with-icon {
    position: relative;
    margin-left: 3rem;

    [class*="icon"] {
        position: absolute;
        top: -0.3rem;
        left: -3rem;

        font-size: 2.5rem;
        line-height: 1;
    }
}