
.timeline {
  margin-top: -97vh;
  display: flex;
  margin-left: auto;
  margin-right: auto;
  flex-wrap: wrap;
  flex-direction: column;
  max-width: 91rem;
  position: relative;

  .dd-none {
    img {
      display: none;
    }
  }
  &:before {
    position: absolute;
    left: 50%;
    width: 2px;
    height: 100%;
    margin-left: -1px;
    content: "";
    background: #ebe9e9;
    height: calc(100% - 300vh);
    top: 100vh;
  }


  .o-youtube-player__img.timeline-img ,
  .o-youtube-player__img.timeline-bg {
    position: inherit;
    margin-top: 2rem;

    &::after {
      content: none;
    }
  }

  .o-youtube-player {
    margin-top: 0;
    z-index: 1;
  }
}

.timeline__content-date {
  font-size: 2.5rem;
  font-weight: bold;
  color: $primary;
  transition: .4s;
  box-sizing: border-box;
}


.timeline-item {
  padding: 3.7rem;
  width: calc(50% - 40px);
  display: flex;
  position: relative;
  transition: .5s;
  background-color: $white;
  border: 1px solid #c7bebe47;
  margin-bottom: 130vh;

  &:last-child {
    margin-bottom: 200vh;
  }

  &:first-child {
    margin-top: 100vh;
  }

  &:nth-child(even) {
    align-self: flex-end;

  .timeline-marker {
    position: absolute;
    border: 0px solid #0e0ef300;
    border-radius: 50%;
    transform: translateY(-50%);
    top: 30%;
    left: -4rem;
    width: 10px;
    height: 10px;
    background: #91ec0b;
    transform: translateX(-50%) translateY(-50%);

    &:before {
        content: '';
        position: absolute;
        top: -8px;
        left: -8px;
        bottom: -8px;
        right: -8px;
        border-radius: 60px;
        border: 2px solid #ebe9e9;
      }
    }
  }

  .timeline-marker {
    position: absolute;
    border: 0px solid #0e0ef300;
    border-radius: 50%;
    top: 30%;
    right: -5rem;
    width: 10px;
    height: 10px;
    background: #91ec0b;
    transform: translateX(-50%) translateY(-50%);

      &:before {
        content: '';
        position: absolute;
        top: -8px;
        left: -8px;
        bottom: -8px;
        right: -8px;
        border-radius: 60px;
        border: 2px solid #ebe9e9;
    }
  }
}

.timeline-item--active {
  opacity: 1;

  &:before {
    top: 50%;
    transition: .3s all .2s;
    opacity: 1;
  }
}


@media only screen and (max-width: 767px) {
  .background-timeline {
    background-position: center;
  }

  .timeline:before {
    left: 2.5rem;
  }

  .timeline-wrapper {
    padding: 0;
  }

  .timeline1col {
    .timeline-item {
      padding-left: 3.7rem;
      padding-right: 3.7rem;
      margin: 10rem auto;
    }
  }

  .timeline-item {
    align-self: baseline !important;
    width: 100%;
    padding: 0 25px 0 50px;
    background: transparent;
    transform: translateY(0);
    margin: 8rem 0;
    border: 0;

    // &:first-child {
    //   margin-top: 0;
    // }
    // &:last-child {
    //   margin-bottom: 0;
    // }

    &:before {
      left: 10px !important;
      padding: 0 !important;
      top: 50px;
      text-align: center !important;
      width: 60px;
      border: none !important;
    }

    .timeline__content {
      background: $white;
      padding: 2rem;
      border: 1px solid #c7bebe47;
    }

      // Marker
    .timeline-marker {
      left: 2.5rem;
      right: unset;
    }

    &:nth-child(even) {
      .timeline-marker {
        left: 2.5rem;
        right: unset;
      }
    }

    &:last-child {
      padding-bottom: 40px;
    }
  }
}

.timeline-container {
  position: relative;
}


// Timeline background
.timeline-wrapper  {
  // padding-top: 5rem;

  .background-timeline {
    height: calc(100vh + 7rem);
    width: 100%;
    position: relative;

    img {
      vertical-align: middle;
      margin: auto;
      transform: translate(-50%, -50%);
      left: 50%;
      right: 50%;
      top: calc(50%);
      position: absolute;
      transition-property: top;
      transition-duration: 1s;
      transition-delay: .1s;
    }
  }

  &:not(.container-content) {
    .background-timeline {
      img {
        // width: 100%;
        // height: auto;

        transform: translateX(-50%);
        left: 50%;
        right: 50%;
        top: 0;
        position: absolute;
        @include img-cover;
      }
    }

  }
}

.background-timeline {
    // position: sticky;
    top: 0;

  &:before {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    content: "";
    overflow: visible;
  }
}

.demo-footer {
  padding: 60px 0;
  text-align: center;

  a {
    color: #999;
    display: inline-block;
    font-family: Cardo;
  }
}

// ----------------Custom timeline
.timeline__content {
  position: relative;
  max-width: 100%;

  >*:last-child {
    margin-bottom: 0;
  }

  >*:first-child {
    margin-top: 0;
  }

  .timeline-minature {
    max-width: 100%;

    .container-content {
      padding: 0;
    }
  }

  .wysiwyg + a.a-link--border-effect,

  .timeline__content-title + a.a-link--border-effect,
  .timeline__content-title + .wysiwyg,

  .timeline__content-date + .wysiwyg,
  .timeline__content-date + a.a-link--border-effect,
  .timeline__content-date + .timeline__content-title,

  .timeline-minature + .wysiwyg,
  .timeline-minature + a.a-link--border-effect,
  .timeline-minature + .timeline__content-title,
  .timeline-minature + .timeline__content-date {
    margin-top: 2rem;
  }

  .timeline-minature + .wysiwyg,
  .timeline-minature + a.a-link--border-effect,
  .timeline-minature + .timeline__content-title {
    margin-top: 2rem;
  }

  .timeline__content-title, .timeline__content-date {
    margin-bottom: 0;
  }

  .timeline-bg {
    display: none;
  }

  .wysiwyg > *:last-child {
    margin-bottom: 0;
  }
}

// Timeline 1 cols
.timeline1col {
  .timeline {
    &::before {
      content: none;
    }
  }

  .timeline-marker {
    display: none;
  }
}

@media only screen and (min-width: 767px) {
  .container-content.container-content--smaller.container-content--fake-col {
    .background-timeline {
      img {
        // top: 0;
        // transform: translateX(-50%);
      }
    }
    .timeline {
      padding-left: 2rem;
        padding-right: 2rem;
    }
  }
  .timeline1col {
    .timeline-item {
      width: 90%;
      transform: translateY(0);
      margin-left: auto;
      margin-right: auto;
    }

    .timeline-item.timeline-item--active ~ .timeline-item {
        transform: translateY(0);
    }

    .timeline-item-wrapper.timeline-item {
      display: flex;
      align-items: center;
    }

    // .mydiv {
    //   height: 100%;
    // }
  }
}


