$ajax-icon-w: 6.4rem;
$ajax-icon-padding: 1rem;
$ajax-icon-color: $primary;

@keyframes lds-dual-ring {
    0% {
      transform: rotate(0deg);
    }

    100% {
      transform: rotate(360deg);
    }
}

/*
HTML généré par Drupal :

<div class="ajax-progress ajax-progress-throbber">
    <div class="throbber">&nbsp;</div>
    <div class="message"></div>
</div>

<div class="ajax-progress ajax-progress-fullscreen">&nbsp;</div>
*/

.ajax-progress-throbber .throbber,
.ajax-progress-fullscreen {
    display: block;
    margin: auto;

    padding: $ajax-icon-padding;
    width: $ajax-icon-w;
    height: $ajax-icon-w;

    background: none;
    background-color: $white;
    border-radius: 100%;

    &::before {
      content: "";
      display: block;
      width: $ajax-icon-w - $ajax-icon-padding*2;
      height: $ajax-icon-w - $ajax-icon-padding*2;

      border: 0.3rem solid $ajax-icon-color;
      border-color: $ajax-icon-color transparent $ajax-icon-color transparent;
      border-radius: 50%;

      animation: lds-dual-ring 1.2s linear infinite;
    }
}

.ajax-progress-fullscreen {
    left: 50%;
    transform: translate(-50%, 0);
}