.o-partenaires {
    .o-paragraph-partenaires {
        .tns-controls {
            button[data-controls] {
                @include icon(before, icon-arrow-thin-right) {
                    // background-color: rgba(63,74,85,.1);
                    padding: 1rem;
                }
                &[disabled] {
                    @include icon(before, icon-arrow-thin-right) {
                        color: rgba(#081624, .29);
                    }
                }
            }
            button[data-controls="prev"] {
                background-color: transparent;
                border: none;
                @include icon(before, icon-arrow-thin-right) {
                    // background-color: rgba(63,74,85,.1);
                    padding: 1rem;
                    color: $gray-900;
                    transform: rotate(180deg);
                }
            }
            button[data-controls="next"] {
                background-color: transparent;
                border: none;
                @include icon(before, icon-arrow-thin-right) {
                    // background-color: rgba(63,74,85,.1);
                    padding: 1rem;
                    color:  $gray-900;
                }
            }
        }
                // Flèches
            .tns-controls {
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                position: absolute;
                left: 50%!important;
                z-index: 1029;
                top: calc(50% - 1.5rem);
                width: 100%;
                transform: translate(-50%);
            }

        // bloc content
        .o-paragraph-partenaires__item {
            .o-paragraph-partenaires__item-inner {
                .o-partenaires__img {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    height: 11rem;
                    max-width: 23rem;
                    margin: auto;
                    img {
                        max-height: 100%
                    }
                    &:first-child {
                        margin-bottom: 4.8rem;
                    }
                }
            }
        }

        // buttons
        .o-paragraph-partenaires__actions {
            margin-top: 4.8rem;
        }
    }
}

// RWD Desktop/tablet
 @include media-breakpoint-up(lg) {
    .o-partenaires {
        h2 {
            margin-bottom: 3rem;
        }
        .o-paragraph-partenaires {
            // Flèches
            .tns-controls {
                // width: calc(100% + 5.8rem);
                button[data-controls] {
                    @include icon(before, icon-arrow-thin-right) {
                        background-color: transparent;
                        padding: 0;
                    }
                }
            }

            .o-paragraph-partenaires__item {
                .o-paragraph-partenaires__item-inner {
                    .o-partenaires__img {
                        &:first-child {
                            margin-bottom: 6rem;
                        }
                    }
                }
            }

            .o-paragraph-partenaires__actions {
                margin-top: 8rem;
            }
        }
    }
 }