.pagination {
    flex-wrap: wrap;
    margin: 3rem 0 5rem 0;
}

.page-link {
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 4.5rem;
    height: 4.5rem;

    .active & {
        font-weight: 600;
    }
}

.page-prev,
.page-next {
    [class*="icon"] {
        font-size: 1.3rem;
    }
}

@include media-breakpoint-up(lg) {
    .pagination {
        margin: 5rem 0 8rem 0;
    }
}