.megamenu {
    margin: 0;
    height: 100%;
    overflow: auto;
}

.megamenu__item:not(:last-child) {
    border-bottom: solid 0.1rem $border-color;
}

.megamenu__link {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;

    width: 100%;
    height: 9rem;
    padding: 2rem 2rem 2rem 8.2rem;
    text-align: left;
    line-height: normal;

    color: $primary;
    border-right: solid 0.5rem transparent;

    transition: background-color 250ms ease, border-color 250ms ease;

    [class*="icon"] {
        position: absolute;
        top: 50%;
        left: 5rem;
        transform: translate(0, -50%);
        font-size: 2rem;
        line-height: 1;
    }

    .icon-external-link {
        display: none;
    }
}

.megamenu__link-title {
    font-size: 1.8rem;
    font-family: $font-FiraSans;
    font-weight: 500;
}

.megamenu__link-desc {
    font-style: italic;
    color: $gray-600;
}

.megamenu__collapse-inner {
    > .container-content {
        max-width: none;
        padding: 0;
    }

    .o-teaser-site__list {
        margin-left: auto;
        margin-right: auto;
        padding: 2rem 5rem;
    }

    .o-banner {
        margin: 0;
    }
}

// variantes univers
.univers--particulier {
    @include megamenu-univers(univers-particulier-primary, univers-particulier-light);

    .m-media-background__btn {
        @extend .btn--univers-particulier;
    }
}
.univers--professionnel {
    @include megamenu-univers(univers-professionnel-primary, univers-professionnel-light);

    .m-media-background__btn {
        @extend .btn--univers-professionnel;
    }
}
.univers--carto {
    @include megamenu-univers(univers-carto-primary, univers-carto-light);

    .m-media-background__btn {
        @extend .btn--univers-carto;
    }
}
.univers--institut {
    @include megamenu-univers(univers-institut-primary, univers-institut-light);

    .m-media-background__btn {
        @extend .btn--univers-institut;
    }
}
.univers--accent {
    @include megamenu-univers(univers-accent-primary, univers-accent-light);

    .m-media-background__btn {
        @extend .btn--accent;
    }
}

@include media-breakpoint-down(md) {
    .megamenu__collapse-inner {
        .o-banner {
            display: none;
        }
    }
}

@include media-breakpoint-up(lg) {
    .megamenu {
        max-width: 40rem;
        padding-top: 5rem;

        border-right: solid 0.1rem $border-color;
    }

    .megamenu__collapse {
        position: absolute;
        left: 40rem;
        top: 0;
        right: 0;
        height: 100vh;
        overflow: auto;
        background-color: $white;
    }
}