.o-paragraph-keyfigures {
    // margin-top: 2rem;
    // margin-bottom: 2rem;
}

.o-paragraph-keyfigure {
    font-size: 1.4rem;
    font-weight: 600;
    text-align: center;

    &:not(&:last-child) {
        margin-bottom: 3rem;
    }
    p {
        margin-bottom: 0;
    }
}

.o-paragraph-keyfigure__number {
    display: block;
    margin-bottom: 1rem;

    font-size: 3.6rem;
    font-family: $font-FiraSans;
    font-weight: 800;
    line-height: 1;

    color: $primary;
}

@include media-breakpoint-up(lg) {
    .o-paragraph-keyfigures {
        // margin-bottom: 0.1rem;
    }

    .o-paragraph-keyfigure {
        &:not(&:last-child) {
            margin-bottom: 0;
        }
        font-size: 1.8rem;
        margin-right: auto;
        margin-left: auto;


        &:nth-child(n+3) {
            margin-top: 4rem;
        }
    }

    .o-paragraph-keyfigure__number {
        font-size: 5rem;
    }
}
