.a-info {
    margin-bottom: 1.5rem;
}

// when there's an icon before text
[class*="a-info-"] {
    display: flex;

    @include icon(before) {
        margin-top: 0.2rem;
        margin-right: 0.8rem;
        font-size: 1.8rem;
    }
}

.a-info-address {
    @include icon(before, icon-location, false);
}

.a-info-date {
    @include icon(before, icon-calendar, false);
}

.a-info-readingtime {
    @include icon(before, icon-timer, false);
}

.a-info-hours {
    @include icon(before, icon-clock, false);
}