.a-date {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    // height: 18rem;
    background-color: $gray-200;
}

.a-date__item {
    display: inline-block;
    width: 11rem;
    line-height: 1;
    clear: left;

    &:not(:first-child) {
        @include icon(before, icon-angle-down) {
            display: block;
            padding: 1rem;
            font-size: 1.2rem;
            text-align: center;
        }
    }
}

.a-date__day {
    float: left;
    width: 6rem;
    margin-right: 1rem;
    text-align: right;
    font-family: $font-FiraSans;
    font-size: 4rem;
    font-weight: $fw-extraBold;
}

.a-date__month {
    margin-top: 0.6rem;
}

.a-date__month,
.a-date__year {
    display: block;
    font-family: $font-FiraSans;
    font-size: 1.4rem;
    font-weight: $fw-medium;
    text-transform: uppercase;
}

.a-date__hour {
    clear: left;
    display: block;
    margin-top: 1.2rem;
    font-weight: $fw-semiBold;
    text-align: center;
}