.ign-commonwish-hp-block--overlap {
    margin-top: -5rem;
    .o-form--wish {
        padding: 2rem;
        .o-form--wish__title {
            text-align: center;
            font-style: italic;
            font-weight: $fw-semiBold;
            font-size: 1.8rem;
        }

        .form-type-select label {
            font-size: 1.5rem;
            font-weight: 500;
            color: #081624;
        }

        .fieldset-legend {
            font-size: 1.5rem;
            font-weight: $fw-medium;
            color: #081624;
        }

        select {
            color: $gray-900;
            font-family: $font-FiraSans;
        }
        .radio-link {
            color: $gray-600;
            background-color: transparent;
            border-color: $gray-600;
            outline-color: $gray-600;
            &:hover {
                color: $gray-800;
                background-color: transparent;
                border-color: $gray-800;
                outline-color: $gray-600;
            }
        }
    }


    // RWD Desktop/tablet
    @include media-breakpoint-up(lg) {
        .o-form--wish {
            padding: 4rem 6.6rem 5rem 5.5rem;
        }
    }
}

.o-form--wish {
    text-align: left;
    form {
        margin-top: $paragraph-margin-bottom;
        display: flex;
        flex-wrap: wrap;
        flex-direction: column;
    }

    .form-group-custom-control {
        order: 1;
        margin: 0;
    }

    .disabled {
        cursor: not-allowed;
        @extend .btn--ghost.btn--gray;
    }

    [type="submit"] {
        align-self: flex-start;
        min-width: 0;
        margin-bottom: $paragraph-margin-bottom;
    }

    .form-radios {
        .form-group {
            margin: 0 1rem 1rem 0;

            &:last-child {
                margin-right: 0;
            }
        }
    }

    .radio-links {
        margin: 1rem 0 0 0;
    }

    .radio-link {
        @extend .btn;
        @extend .btn--ghost;
        @extend .btn-sm;

        .modal & {
            @extend .btn--ghost.btn--gray;
        }

        .o-page-title & {
            @extend .btn--ghost.btn--white;
        }
    }

    @include media-breakpoint-up(lg) {
        form {
            flex-direction: row;
            align-items: flex-end;
        }

        .form-type-select {
            @include aw-make-col-auto;
            margin-right: 3rem;
        }

        .form-group-custom-control {
            @include aw-make-col-value(100%);
        }

        [type="submit"] {
            align-self: initial;
        }

        .form-radios {
            display: flex;
            flex-wrap: wrap;
        }
    }
}
