.o-teasers {
    margin-top: $paragraph-margin-bottom;
    margin-bottom: $paragraph-margin-bottom;

    > .container-content {
        position: relative; // needed to position absolute bouton (.o-teasers__more)
    }
}

.o-teasers__title {
    margin-bottom: 3rem;
}

.o-teasers__more {
    text-align: right;
}

.o-teasers__load-more {
    margin-bottom: 1rem;
    font-family: $font-FiraSans;
    font-weight: 500;
    color: $gray-600;
}

@include media-breakpoint-up(sm) {
    .o-teasers {
        margin-top: 5rem;
        margin-bottom: 5rem;
    }

    .o-teasers--oneHighlight {
        @include aw-make-row(2rem);

        .o-teasers__item {
            @include aw-make-col-ready(2rem);
            @include aw-make-col-value(50%);

            &:first-child {
                @include aw-make-col-value(100%);
            }

            display: flex;

            > * {
                width: 100%;
            }
        }
    }
}

@include media-breakpoint-up(md) {
    .o-teasers--3by3 {
        @include aw-make-row(2rem);

        .o-teasers__item {
            @include aw-make-col-ready(2rem);
            @include aw-make-col-value(33.333%);

            display: flex;

            > * {
                width: 100%;
            }
        }
    }

    .o-teasers--oneHighlight {
        .o-teasers__item {
            &:nth-child(n+4) {
                @include aw-make-col-value(33.333%);
            }
        }
    }

    .o-teasers__more {
        position: absolute;
        top: 0;
        right: $grid-gutter-width;
    }
}

@include media-breakpoint-up(lg) {
    .o-teasers__more {
        top: 1rem;
    }
}
