blockquote {
    margin: 3rem 0;
    padding: 2rem 1rem;

    font-size: 1.4rem;
    font-weight: 600;
    font-style: italic;

    background-color: $gray-100;

    border-left: solid 0.2rem $gray-400;

    p:last-child {
        margin-bottom: 0;
    }

    @include media-breakpoint-up(lg) {
        margin: 4rem 0 5rem 0;
        padding: 2.5rem 2.5rem 2.5rem 3.8rem;

        font-size: 1.8rem;

        border-left-width: 0.6rem;
    }
}