$masonry-gutter: 2rem;

@include media-breakpoint-up(md) {
    .o-socialwall {
        margin-left: -$masonry-gutter*0.5;
        margin-right: -$masonry-gutter*0.5;
    }

    .o-socialwall__item {
        width: 50%;
        padding-left: $masonry-gutter*0.5;
        padding-right: $masonry-gutter*0.5;
    }
}

@include media-breakpoint-up(lg) {
    .o-socialwall__item {
        width: 33.333%;
    }
}