$drupal-toolbar-height: 3.9rem;
$drupal-toolbar-height-open: 7.9rem;

.adminimal-admin-toolbar.user-logged-in {
    .header-principal {
        top: $drupal-toolbar-height;
        z-index: 3;
    }

    .navbar-collapse {
        top: $drupal-toolbar-height;
    }

    @include media-breakpoint-up(lg) {
        .navbar-collapse {
            top: $drupal-toolbar-height + $header-height;
        }

        &.toolbar-tray-open {
            .header-principal {
                top: $drupal-toolbar-height-open;
            }

            .navbar-collapse {
                top: $drupal-toolbar-height-open + $header-height;
            }
        }
    }
}

.user-login-form {
    @extend .container-content;
    @include container-content--smaller;

    margin-top: 5rem;
    margin-bottom: 5rem;
}

// Filters
.paragraph--type--job-list {
    z-index: 2!important;
}
.job-filters-dropdown .form--inline {
    details {
        border: none;
        &[open] .details-wrapper {
            border: .1rem solid $gray-300;
            border-top: none;
        }
        summary {
            position: relative;
            height: 5rem;
            padding: 1.3rem 1.5rem;
            line-height: 1.5;
            border: .1rem solid $gray-300;

            &::marker {
                content: none;
            }

            @include icon(after, icon-angle-down) {
                font-size: 1rem;
                font-weight: $fw-bold;
                right: 6px;
                top: 50%;
                position: absolute;
                transform: translateY(-50%);
            }

           &[aria-expanded="true"] {
                @include icon(after, icon-angle-up) {
                    font-size: 1rem;
                    font-weight: $fw-bold;
                }
            }


        }
        // selectionnez tout
        .details-wrapper {
            .bef-toggle {
                margin-bottom: 2rem;
                display: block;
            }

            // Apply scroll [Desktop]
            // >.form-checkboxes ul {
            //     max-height: 15rem;
            //     overflow: auto;
            // }
        }
    }
}
@include media-breakpoint-up(lg) {
    .job-filters-dropdown > form > .form--inline {
        display: flex;
        flex-direction: row;

        details {
            position: relative;
            flex: 0 33.33%;
            margin-right: 3rem;

            &:last-of-type {
                margin-right: 0;
            }

            .details-wrapper {
                position: absolute;
                background: $white;
                width: 100%;
                z-index: 99;

                // Apply scroll [Mobile]
                // >.form-checkboxes ul {
                //     max-height: 16rem;
                // }
            }

            &+.form-actions  {
                display: flex;
                align-items: center;
                margin-left: 3rem;
            }
        }
    }
}