.o-mea {
	h2 {
		text-align: center;
		margin-bottom: 4rem;
	}
	.mea-container {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;


		.mea-item {
			// flex: 1 0 31.5rem;
			// width: 31.5rem;
			margin-bottom: 6rem;
			text-align: center;
			&[class*="bg-"] {
				padding: 5rem 2.3rem 5.5rem;
			}
			&:last-child {
				// margin-right: 0;
				margin-bottom: 0;
			}
			h3 {
				color: $primary;
				text-align: center;
				margin: 1rem 0;
			}

			p {
				line-height: 3rem;
			}
		}
	}
}
// RWD Desktop/tablet
 @media (min-width: 61.98em) and (max-width: 26em){
	.o-mea {
		h2 {
			margin-bottom: 8rem;
		}
		.mea-container {
			.mea-item {
				&:nth-child(n+3) {
					margin-top: 6rem;
				}
			}
		}
	}
 }
 @include media-breakpoint-up(sm) {
	.o-mea {
		h2 {
			margin-bottom: 6rem;
		}
		.mea-container {
			.mea-item {
				flex: 1 0 calc((100% / 3) - 6rem);
				width: calc((100% / 3) - 6rem);
				max-width: calc((100% / 3) - 6rem);
				margin-left: 3rem;
				margin-right: 3rem;
				margin-bottom: 0;
				&:nth-child(n+4) {
					margin-top: 6rem;
				}

				// &:nth-child(n+3) {
				// 	margin-right: 0;
				// }
				// padding: 5rem 2.3rem 5.5rem;

				.mea-img {
					margin-bottom: .5rem;
				}
			}
		}
	}
}