.float-target-parent {
    position: relative;

    label {
        pointer-events: none;

        position: absolute;
        top: 50%;
        left: $input-padding-x;
        z-index: 10;

        font-family: inherit;

        transform: translateY(-50%);
        transition: transform 0.3s ease, font-size 0.3s ease, color 0.3s ease;

        .form-group-textarea & {
            top: 2.6rem;
        }
    }

    .form-control {
        padding-top: 2rem;
        padding-bottom: 0.7rem;
    }


}

.float-target-parent.focused {
    label {
        font-size: 1rem;

        transform: translateY(-2rem);
        transition: transform 0.3s ease, font-size 0.3s ease, color 0.3s ease;
    }
}
