.o-banner {
    margin: 5rem -#{$grid-gutter-width};
    line-height: 2;
    text-align: center;
}

.o-banner__inner {
    width: 100%;

    > .icon-external-link {
        display: none;
    }
}

.o-banner__inner--smaller {
    max-width: 58.8rem;
    margin-left: auto;
    margin-right: auto;
}

.o-banner--bg {
    position: relative;
    z-index: 1;

    display: flex;
    flex-direction: column;

    // need an inner container to center content
    // see https://github.com/philipwalton/flexbugs#flexbug-3
    .o-banner__inner {
        display: flex;
        align-items: center;
        justify-content: center;
        min-height: 50.6rem;
        padding: 3rem 0;

        > * {
            margin-left: 0; // fix flexbug IE
            margin-right: 0; // fix flexbug IE
        }

        .container-content {
            width: 100%;
        }
    }

    > * {
        position: relative;
    }

    * {
        color: $white;
    }
}

.o-banner__subtitle {
    margin-bottom: 0;
    font-weight: 800;
}

.o-banner__desc {
    margin-bottom: 1rem;
}

.o-banner__btn {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;

    .btn {
        margin: 1rem;

        // extends link zone
        &::after {
            content: "";
            display: block;
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
        }
    }
}

@include media-breakpoint-up(md) {
    .o-banner--bg .o-banner__inner {
        min-height: 40rem;
    }
}

@include media-breakpoint-up(lg) {
    .o-banner {
        margin: 5rem 0;
    }
}