.o-page-title {
    margin-bottom: $paragraph-margin-bottom;
    text-align: center;
}

.o-page-title__inner {
    width: 100%;
    padding-top: 5rem;
}

.o-page-title--bg {
    position: relative;
    display: flex;
    flex-direction: column;



    &.block-space, &.small-space {
        margin-top: 0;
    }
    // need an inner container to center content
    // see https://github.com/philipwalton/flexbugs#flexbug-3
    .o-page-title__inner {
        display: flex;
        align-items: center;
        justify-content: center;
        min-height: 50.6rem;
        padding: 3rem 0;

        > * {
            margin-left: 0; // fix flexbug IE
            margin-right: 0; // fix flexbug IE
        }

        .container-content {
            width: 100%;
        }
    }

    > * {
        position: relative;
    }

    &, h1, label {
        color: $white;
    }

    .form-control:focus {
        outline-color: $white;
    }

    h1 {
        max-width: 48.7rem;
        margin-left: auto;
        margin-right: auto;
    }

    .lead {
        max-width: 65.2rem;
        margin-left: auto;
        margin-right: auto;
        font-style: italic;
    }
}

.o-page-title__tag {
    margin-bottom: 2rem;
    text-align: center;
}

.o-page-title__logo {
    margin-bottom: 1rem;
}

.o-page-title__infos {
    text-align: center;
    font-style: italic;
    color: $gray-600;

    [class*="a-info-"] {
        justify-content: center;
    }
}

.o-page-title__btn {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;

    .btn {
        margin: 1rem;
    }
}

.o-page-title--home {
    .o-page-title__inner {
        padding-top: 6rem;
        padding-bottom: 10rem;
    }

   .lead {
        max-width: none;
    }

    .h1, h1 {
        max-width: 22ch!important;
        margin-left: auto;
        margin-right: auto;
    }
    .m-media-background__btn {
        top: 0;
        bottom: auto;
    }
}

@include media-breakpoint-up(sm) {
    .o-page-title__infos {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;

        .a-info {
            margin-bottom: 0;

            + .a-info {
                position: relative;
                margin-left: 0.5rem;
                padding-left: 1rem;

                &::after {
                    content: "-";
                    position: absolute;
                    top: 50%;
                    left: 0;
                    transform: translate(0, -50%);
                }
            }
        }
    }
}

@include media-breakpoint-up(md) {
    .o-page-title--bg .o-page-title__inner {
        min-height: 40rem;
    }

    .o-page-title--home {
        .o-page-title__inner {
            min-height: 40rem;
        }
    }
}

@include media-breakpoint-up(lg) {
    .o-page-title--home {
        .container-content--smaller {
            padding-left: $grid-gutter-width;
            padding-right: $grid-gutter-width;

            @include aw-make-col-value(91rem);
        }
    }
}