.view-paragraph-news-list {
    .form--inline {
        display: flex;
        flex-wrap: wrap;
        align-items: flex-end;
    }

    .form-group-custom-control {
        margin-right: auto;

        @include aw-make-col-value(100%);
    }

    .form-checkboxes {
        display: flex;
        flex-wrap: wrap;
        margin: 0;
        padding-left: 0;
        min-height: 0;

        .form-type-checkbox {
            position: relative;
            margin-right: 1rem;
        }

        input[type="checkbox"] {
            position: absolute;
            left: 0;
            right: 0;
            width: 100%;
            height: 100%;
            opacity: 0;
        }

        label {
            position: relative;
            cursor: pointer;
            margin: 0.5rem 0;

            @extend .btn;
            @extend .btn--ghost;
            @extend .btn--gray;
            @extend .btn-sm;
        }

        input[type="checkbox"]:checked + label {
            @extend .btn--primary;

            &::after {
                content: "";
                position: absolute;
                top: auto;
                bottom: 0.4rem;
                left: 0;
                right: 0;
                width: 0.4rem;
                height: 0.4rem;
                margin-left: auto;
                margin-right: auto;
                border-radius: 100%;
                background-image: none;
                background-color: currentColor;
            }
        }
    }

    .custom-control-label:after,
    .custom-control-label:before {
        content: none;
    }
}

[data-drupal-selector="edit-field-theme-target-id"] > legend {
    @include sr-only;
}

.view-paragraph-news-list__input {
    margin-bottom: $form-group-margin-bottom;

    .form-item-search {
        @include aw-make-col-auto;
    }

    .form-actions {
        @include drupal-submit-into-search;
    }
}

@include media-breakpoint-up(sm) {
    .view-paragraph-news-list {
        .o-teasers {
            margin-top: 1rem;
        }

        .form-type-select {
            margin-left: auto;
            display: flex;
            align-items: center;
            justify-content: flex-end;

            label {
                margin: 0 1rem 0 0;
            }

            select {
                display: inline-block;
                width: auto;
                vertical-align: middle;
            }
        }

        .form-group-custom-control {
            @include aw-make-col-auto(); // fix IE
            margin-bottom: 0;
        }
    }
}

@include media-breakpoint-up(lg) {
    .view-paragraph-news-list {
        .form--inline {
            justify-content: center;
        }
    }

    .view-paragraph-news-list__input {
        .form-item-search {
            @include aw-make-col-value(66rem);
        }
    }
}