.o-formation {
    .o-formation__title {
        margin-bottom: 2rem;
    }
    +.o-formation {
        margin-top: 2rem;
    }
    .formation_top,
    .formation-bottom,
    .formation-icons,
    .formation-actions {
        flex-direction: column;
    }
    .formation-item {
        padding: 3rem 4rem 3rem;
        &:not([class*='bg-']) {
			padding: 0;
		}
    }

    .formation_top {
        display: flex;
        justify-content: space-between;
        .formation-subtitle {
            color: $primary;
            font-size: 1.8rem;
            margin-bottom: 1rem;
        }

        .a-info {
            @include icon(before) {
                color: $primary;
                font-size: 2rem;
                vertical-align: bottom;
                margin-right: 1rem;
            }
        }
    }

    .formation-bottom {
        display: flex;
        justify-content: space-between;

        .formation-icons {
            display: flex;
            flex-wrap: wrap;
            .a-info {
                font-size: 1.5rem;
                display: flex;
                @include icon(before) {
                    color: $primary;
                    font-size: 2rem;
                    vertical-align: bottom;
                    margin-right: 1rem;
                }
                &:last-child {
                    margin-bottom: 0;
                }
            }
        }

        .formation-actions {
            display: flex;
            justify-content: center;
            text-align: center;
            margin-top: 4rem;
            .btns:not(:last-child){
                margin-bottom: 1rem;
            }
        }
    }
}

// RWD Desktop/Tab
@include media-breakpoint-up(md) {
    .o-formation {
        .formation_top,
        .formation-bottom,
        .formation-icons,
        .formation-actions {
            flex-direction: row;
        }


        .formation-bottom {
            .formation-icons {
                flex: 1 0 50%;
                .a-info {
                    flex: 1 0 calc(50% - 1rem);
                    margin-right: 1rem;
                }
            }
            .formation-actions {
                flex: 1 0 50%;
                justify-content: flex-end;
                margin-top: 0;
                .btns {
                    margin-bottom: 0;
                    &:not(:last-child){
                        margin-right: 1rem;
                    }
                }
            }
        }
    }
}

// Formation filter
.view-formations .form--inline .form-item {
    width: 100%;
    min-width: 100%;

    &:not(:last-child) {
        margin-bottom: 2rem;
    }
}

@include media-breakpoint-up(md) {
    .view-formations .form--inline .form-item {
        width: 30rem;
        min-width: 30rem;
        margin-bottom: 0;

        &:not(:last-child) {
            margin-right: 2rem;
        }
    }
}